@img_path: '../img';

.materials-form {
  .form-material {
    position: relative;
  }
  .form-material > label {
    position: absolute;
    top: 6px;
    left: 0;
    font-size: 13px;
    font-weight: 600;
    -webkit-transform: translateY(-24px);
    -ms-transform: translateY(-24px);
    transform: translateY(-24px);
  }
  .form-material.floating > label {
    font-size: 15px;
    font-weight: 400;
    cursor: text;
    z-index: 10;
    -webkit-transition: all 0.15s ease-out;
    transition: all 0.15s ease-out;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
  .form-material.floating > .form-control[disabled] + label,
  .form-material.floating > .form-control[readonly] + label,
  fieldset[disabled] .form-material.floating > label {
    cursor: not-allowed;
  }
  .form-material > .form-control {
    padding-left: 0;
    padding-right: 0;
    border: 0;
    border-radius: 0;
    background-color: transparent;
    -webkit-box-shadow: 0 1px 0 #e6e6e6;
    box-shadow: 0 1px 0 #e6e6e6;
  }
  .form-material > .form-control:focus {
    background-color: transparent;
    -webkit-box-shadow: 0 2px 0 #646464;
    box-shadow: 0 2px 0 #646464;
  }
  .form-material > .form-control:focus + label {
    color: #646464;
  }
  .form-material > .form-control:focus ~ .input-group-addon {
    -webkit-box-shadow: 0 2px 0 #646464;
    box-shadow: 0 2px 0 #646464;
  }
  .form-material > .form-control:focus + label,
  .form-material.floating.open > label {
    font-size: 13px;
    font-weight: 600;
    cursor: default;
    -webkit-transform: translateY(-24px);
    -ms-transform: translateY(-24px);
    transform: translateY(-24px);
  }
  .form-material .form-control[disabled],
  .form-material .form-control[readonly],
  .form-material fieldset[disabled] .form-control {
    background-color: #fff;
    border-bottom: 1px dashed #ccc;
    -webkit-box-shadow: none;
    box-shadow: none;
  }
  .form-material.input-group .input-group-addon {
    border: none;
    background-color: transparent;
    border-radius: 0 !important;
    -webkit-box-shadow: 0 1px 0 #e6e6e6;
    box-shadow: 0 1px 0 #e6e6e6;
    -webkit-transition: all 0.15s ease-out;
    transition: all 0.15s ease-out;
  }
  .form-material.form-material-primary > .form-control:focus {
    -webkit-box-shadow: 0 2px 0 #5c90d2;
    box-shadow: 0 2px 0 #5c90d2;
  }
  .form-material.form-material-primary > .form-control:focus + label {
    color: #5c90d2;
  }
  .form-material.form-material-primary > .form-control:focus ~ .input-group-addon {
    color: #5c90d2;
    -webkit-box-shadow: 0 2px 0 #5c90d2;
    box-shadow: 0 2px 0 #5c90d2;
  }
  .form-material.form-material-success > .form-control:focus {
    -webkit-box-shadow: 0 2px 0 #46c37b;
    box-shadow: 0 2px 0 #46c37b;
  }
  .form-material.form-material-success > .form-control:focus + label {
    color: #46c37b;
  }
  .form-material.form-material-success > .form-control:focus ~ .input-group-addon {
    color: #46c37b;
    -webkit-box-shadow: 0 2px 0 #46c37b;
    box-shadow: 0 2px 0 #46c37b;
  }
  .form-material.form-material-info > .form-control:focus {
    -webkit-box-shadow: 0 2px 0 #70b9eb;
    box-shadow: 0 2px 0 #70b9eb;
